<template>
<div class="container-fruid ">
  <div class="d-flex justify-content-center fs-4">
  <router-link to="/shop" >回到商品列表</router-link>
  |
  <router-link to="/pay" >結帳</router-link>
  </div>
  <h2 class="d-flex justify-content-center fs-3">金額總計：{{totalPrice}}</h2>

  <div class="container-fruid  bg-dark py-5">
  <ul class="row d-flex flex-wrap align-items-center justify-content-center ">
    <li class="card col-3 px-3 pt-3 me-3 mb-5" v-for="(product,index) in products" :key="product.id">
    <img class="card-img-top" :src="product.img" />
    <div class="d-flex flex-nowrap justify-content-between my-1" style="font-weight:bolder;">
      <h2 class="my-auto fs-4" style=" color:#484D79">{{product.name}}</h2>
    </div>
    <div class="d-sm-inline-flex flex-wrap justify-content-between my-1">
      <h4 class="text-nowrap fs-5" style=" color:#484D79">JPY¥ {{product.price}}</h4>
    </div>
    <h4 class="fs-5">小計:{{product.price*product.amount}}</h4>
    </li>
  </ul>
  </div>
</div> 

</template>

<script>
import Product from './shop.vue'
export default{
  components:{
    Product
    },
  data(){
    return{
    products:[]
  }},
  computed:{
    totalPrice(){
      return this.products.reduce(function(total,product){
      return total+product.price*product.amount
    },0)
    
    }
  },
  mounted(){
    this.products=localStorage.getItem("cart");
    this.products=JSON.parse(this.products)
  },
  

}

</script>