      }},
      
    addOne (product) {
       if(product.amount+product.addCartNumber <10){
        product.addCartNumber++;
      }else{
        alert('最大購買數量為10件')
      }
        },
      cleanAmount(product){
        product.amount=0;
      },
      addCart(product){
        let flag=false
        this.cart.map(function(item){
          if(item.id==product.id){
            item.amount+=product.addCartNumber;
            flag=true;
            product.addCartNumber=0
          };          
          return
          })
        if(flag==false){
          product.amount=product.addCartNumber;
          this.cart.push(product);
          
        }
        localStorage.setItem("cart",JSON.stringify(this.cart));
        
