import { createRouter, createWebHistory } from 'vue-router'
import indexView from '../view/index.vue'
import hololiveView from '../view/hololive.vue'
import majView from '../view/maj.vue'
import shopView from '../view/shop.vue'
import cartView from '../view/cart.vue'
import payView from '../view/pay.vue'
import signUpView from '../view/signUp.vue'
import loginView from '../view/login.vue'

const routes = [
    {
      path:'/',
      redirect:'/index'
    },
    {
      path: '/index',
      name: 'indexView',
      component: indexView
    },
    {
      path: '/hololive',
      name: 'hololiveView',
      component: hololiveView
    },
    {
      path: '/maj',
      name: 'majView',
      component: majView
    },
    {
      path: '/shop',
      name: 'shopView',
      component: shopView
    },
    {
      path: '/cart',
      name: 'cartView',
      component: cartView
    },
    {
      path: '/pay',
      name: 'payView',
      component: payView
    },
    {
      path: '/signUp',
      name: 'signUpView',
      component: signUpView
    },
    {
      path: '/login',
      name: 'loginView',
      component: loginView
    },
   
  ]
  const router = createRouter({
    history: createWebHistory(),
    routes
  })
  
  export default router
  